import React, { useState, useEffect } from 'react';
import logo from './logo.svg';
import './Search.scss';
import Slider from '@mui/material/Slider';
import Grid from '@mui/material/Grid';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import { CardActionArea } from '@mui/material';
import { styled } from '@mui/material/styles';
import Paper from '@mui/material/Paper';
import OutlinedInput from '@mui/material/OutlinedInput';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select, { SelectChangeEvent } from '@mui/material/Select';
import { Theme, useTheme } from '@mui/material/styles';
import Button from '@mui/material/Button';
import Modal from '@mui/material/Modal';
import Box from '@mui/material/Box';

import FavoriteBorderSharpIcon from '@mui/icons-material/FavoriteBorderSharp';
import FavoriteIcon from '@mui/icons-material/Favorite';
import Client from './Client';

function Faviorte() {
    const [properties, setProperties] = useState([])


    useEffect(() => {
        fetchProperties()
    }, [])


    const fetchProperties = () => {
        fetch("http://localhost:8898/properties?interested=true")
            .then(response => {
                return response.json()
            })
            .then(data => {
                console.log(data);
                setProperties(data);
            })
    }


    return (
        <div>
            <Client></Client>
            <div style={{ width: '100%', height: '100vh', margin: 40 }}>
                <Grid container spacing={2}>
                    {[...properties].map((x, i) =>
                        <Grid xs={3} key={i}>
                            <Card sx={{ maxWidth: 345 }} key={i}>
                                <CardActionArea>
                                    <CardMedia
                                        component="img"
                                        height="140"
                                        image="https://source.unsplash.com/random/?house,building,real+estate"
                                    />
                                    <CardContent>
                                        <Typography gutterBottom variant="h5" component="div">
                                            {x['name']}
                                        </Typography>
                                        <Typography variant="body2" color="text.secondary">
                                        <i>Year of construction:</i> <b>{x['year']}</b>
                                        </Typography>
                                        <Typography variant="body2" color="text.secondary">
                                                    Price: {x['price']}
                                        </Typography>
                                    </CardContent>
                                </CardActionArea>
                            </Card>
                        </Grid>
                    )}
                </Grid>
            </div>
        </div>
    );
}

export default Faviorte;
