import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import './PropertyDetail.scss';

interface Property {
    id: number;
    name: string;
    city: string;
    price: number;
    interested: boolean;
    image: string;
    year: number;
}

function PropertyDetail() {
    const { id } = useParams();
    const [properties, setProperties] = useState<Property|null>(null);


    useEffect(() => {
        fetchProperties()
    }, [])


    const fetchProperties = () => {
        fetch("http://localhost:3001/properties/" + id)
            .then(response => {
                return response.json()
            })
            .then(data => {
                const property: Property = data as Property;
                setProperties(property);
            })
    }

    console.log(properties);
    return (
        <div>
            <div className="a">
                <div className="images">
                    <img src={"https://source.unsplash.com/random/?house,building,real+estate"} alt="Logo" />
                </div>
                <div className="info">
                    {properties == undefined && <div className="title">
                        <h1>Loading...</h1>
                    </div>
                    }
                    {properties !== undefined &&
                        <div style={{margin: 10}}>
                            <div className="title">
                                <h1>{properties?.name}</h1>
                            </div>
                            <div className="price">
                                <h3>Price: {properties?.price}</h3>
                            </div>
                            <div className="city">
                                <h3>City: {properties?.city}</h3>
                            </div>
                            <div className="year">
                                <h3>Year of construction: {properties?.year}</h3>
                            </div>

                        </div>
                    }
                </div>
            </div>
        </div>
    );
}

export default PropertyDetail;
